import { Chip } from '@mui/material';
import { baseImgURL } from 'assets/Constant';
import db from 'libs/db';
import React from 'react';
import Spinner from 'react-bootstrap/Spinner';
import { BsChevronLeft, BsChevronRight } from 'react-icons/bs';
import { FiSearch } from 'react-icons/fi';
import InfiniteScroll from 'react-infinite-scroll-component';
import { toast } from "react-toastify";
import AdminServices from 'services/api/AdminServices';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';
import { CurrencyFormat } from 'views/mobileView/components/atoms/CurrencyFormat';
import MintboxIcon from 'views/mobileView/components/atoms/MintboxIcon';
import ReadMoreText from 'views/mobileView/components/atoms/ReadMoreText';
import RedGreenText from 'views/mobileView/components/atoms/RedGreenText';
import { RiskFormat } from 'views/mobileView/components/atoms/RiskFunctions';
import MarketingHeader from 'views/mobileView/components/molecules/Headers/MarketingHeader';
import cssVar from 'views/mobileView/styles';

export default function AllFunds(props) {
    const type = props.location.state.type ? props.location.state.type : 3;
    const query = props.location.state.query ? props.location.state.query : 'All';
    const RISKLEVEL = props.location.state.RISKLEVEL ? props.location.state.RISKLEVEL : 0;
    const DURATIONID = props.location.state.DURATIONID ? props.location.state.DURATIONID : 0;

    const [clientData, setClientData] = React.useState()
    const [loading, setLoading] = React.useState(true)

    const [allFunds, setAllFunds] = React.useState([])
    const [watchlist, setWatchlist] = React.useState([])
    const [filteredFunds, setFilteredFunds] = React.useState([])
    const [data, setData] = React.useState([]) // used to display

    const [selectedFiltersArray, setSelectedFiltersArray] = React.useState([query])
    const [searchText, setSearchText] = React.useState('');
    const [duration, setDuration] = React.useState(0); // 0-> 1yr, 1-> 3yr, 2-> 5yr
    const [searchResult, setSearchResult] = React.useState([])
    const [sortResult, setSortResult] = React.useState([])


    const chipset = type === 1 ? [
        { "key": "All", "name": "All" },
        { "key": "IsEquityFund", "name": "Equity" },
        { "key": "IsForeignFund", "name": "Foreign" },
        { "key": "IsLiquidFund", "name": "Liquid" },
        { "key": "IsGoldFund", "name": "Gold" },
        { "key": "IsDebtFund", "name": "Debt" },
        { "key": "IsSectoralFund", "name": "Sectoral" },
        { "key": "IsFactoralFund", "name": "Factor" },
        { "key": "IsThematicFund", "name": "Thematic" },
        { "key": "Watchlist", "name": "Watchlisted" },
    ]
        :
        type === 2 ? [
            { "key": "All", "name": "All" },
            { "key": "IsIndexFund", "name": "Index" },
            { "key": "IsLargeCapFund", "name": "Large cap" },
            { "key": "IsFlexiCapFund", "name": "Flexi cap" },
            { "key": "IsUSEquity", "name": "US equity" },
            { "key": "IsGoldFund", "name": "Gold" },
            { "key": "Watchlist", "name": "Watchlisted" },
        ]
            : [
                { "key": "All", "name": "All" },
                // { "key": "Fee", "name": "Free" },
                { "key": "WealthCreator", "name": "Wealth creator" },
                // { "key": "Watchlist", "name": "Watchlisted" },
                // { "key": "Recommended", "name": "Recommended for you" },
                // { "key": "Watchlist", "name": "Watchlisted" },
                { "key": "RiskLevel", "name": RiskFormat(RISKLEVEL), "show": RISKLEVEL !== 0 },
                { "key": "DurationId", "name": DURATIONID === 1 ? "1-3 year" : DURATIONID === 2 ? "3-5 year" : DURATIONID === 3 ? "5+ years" : DURATIONID === 15 ? 'Short term ' : "", "show": DURATIONID !== 0 },
            ]

    React.useEffect(() => {
        document.getElementsByClassName("normalChip selected")[0].scrollIntoView()
    }, [selectedFiltersArray])

    React.useEffect(async () => {
        await loadData();
        setLoading(false);
    }, [])

    React.useEffect(() => {
        applyFilter()
    }, [allFunds, selectedFiltersArray])

    React.useEffect(() => {
        onSearch(searchText);
    }, [filteredFunds])

    React.useEffect(() => {
        onSort(duration)
    }, [searchResult])


    // React.useEffect(() => {
    //     loadData()
    // }, [])

    // React.useEffect(() => {
    //     applyFilter()
    // }, [allFunds, selectedFiltersArray])

    // React.useEffect(() => {
    //     onSearch();
    // }, [filteredFunds, searchText, selectedSortOption])

    // React.useEffect(() => {
    //     console.log(searchResult)
    //     setData(searchResult.slice(0, 25)) // set initial 25 funds
    //     // setLoadingFilter(false)
    // }, [searchResult])

    const applyFilter = () => {
        console.log("Apply filter", selectedFiltersArray, allFunds)
        if (type === 3) {
            // for mintboxes
            if (selectedFiltersArray.includes('All')) {
                setFilteredFunds(allFunds.filter(fund => fund.IsCustom === 0))
                return
            }
            if (selectedFiltersArray.includes("Fee")) {
                setFilteredFunds(allFunds.filter(fund => fund["Fee"] === "0" && fund.IsCustom === 0))
            } else if (selectedFiltersArray.includes("RiskLevel")) {
                setFilteredFunds(allFunds.filter(fund => fund["RiskLevel"] === RISKLEVEL && fund.IsCustom === 0))
            } else if (selectedFiltersArray.includes("DurationId")) {
                if (DURATIONID === 15) {
                    setFilteredFunds(allFunds.filter(fund => fund["DurationId"] === 1 || fund["DurationId"] === 2))
                } else {
                    setFilteredFunds(allFunds.filter(fund => fund["DurationId"] === DURATIONID && fund.IsCustom === 0))
                }
            } else if (selectedFiltersArray.includes("Recommended")) {
                if (clientData?.RiskInfo.length) {
                    setFilteredFunds(allFunds.filter(fund => fund.RiskLevel === clientData.RiskInfo[0].RiskScore && fund.IsCustom === 0))
                }
            } else if (selectedFiltersArray.includes("WealthCreator")) {
                setFilteredFunds(allFunds.filter(fund => fund.DurationId === 3))
            } else if (selectedFiltersArray.includes('Watchlist')) {
                console.log(watchlist)
                setFilteredFunds(allFunds.filter(fund => !fund.IsCustom && watchlist.some(w => Number(w.FundCode) === fund.MintboxId)))
            }
        } else if (type === 2) {
            // for mutual funds
            if (selectedFiltersArray.includes('All')) {
                setFilteredFunds(allFunds)
                return
            }
            setFilteredFunds(allFunds.filter(fund => {
                return selectedFiltersArray.some(filter => {
                    if (filter === 'Watchlist') {
                        return watchlist.some(w => Number(w.FundCode) === fund.FundCode)
                    } else {
                        return fund[filter] === 1
                    }
                })
            }))
        } else if (type === 1) {
            // for ETFs
            if (selectedFiltersArray.includes('All')) {
                setFilteredFunds(allFunds)
                return
            }
            setFilteredFunds(allFunds.filter(fund => {
                return selectedFiltersArray.some(filter => {
                    if (filter === 'Watchlist') {
                        return watchlist.some(w => w.FundCode === fund.Symbol)
                    } else {
                        return fund[filter] === 1
                    }
                })
            }))
        }
    }


    const loadData = async () => {
        setLoading(true);
        if (type === 1) {
            try {
                await db.get(db.model.allEtfs).then(async (u) => {
                    if (u) {
                        setAllFunds(u);
                        setLoading(false);
                    } else {
                        let res = await AdminServices.getAllETFs().then(res => res);
                        if (res.type === true) {
                            console.log("Get All ETFs API: ", res.data)
                            await db.set(db.model.allEtfs, JSON.stringify(res.data))
                            setAllFunds(res.data)
                            setLoading(false);
                        }
                    }
                })
            } catch (error) {
                setLoading(false);
                toast.error(error);
            }
        } else if (type === 2) {
            try {
                await db.get(db.model.allMutualFunds).then(async (u) => {
                    if (u) {
                        setAllFunds(u);
                        setLoading(false);
                    } else {
                        let res = await AdminServices.getFundsData().then(res => res)
                        if (res.type === true) {
                            console.log("Get All MFs API: ", res.data)
                            await db.set(db.model.allMutualFunds, JSON.stringify(res.data))
                            setAllFunds(res.data)
                            setLoading(false);
                        }
                    }
                })
            } catch (error) {
                setLoading(false);
                toast.error(error);
            }
        } else {
            try {
                await db.get(db.model.allMintboxes).then(async (u) => {
                    if (u) {
                        setAllFunds(u);
                        setLoading(false);
                    } else {
                        let res = await AdminServices.getAllMintboxes(0).then(res => res);
                        if (res.type === true) {
                            console.log("Get All Mintboxes API: ", res.data)
                            await db.set(db.model.allMintboxes, JSON.stringify(res.data))
                            setAllFunds(res.data)
                            setLoading(false);
                        }
                    }
                })
            } catch (error) {
                setLoading(false);
                toast.error(error);
            }
        }
    }

    const onSearch = async (t) => {
        console.log("search", t, filteredFunds)
        let temp = [].concat(filteredFunds)
        if (t.length > 0) {
            const textArray = t.split(' ');
            if (type === 1) {
                temp = filteredFunds.filter(fund => textArray.every(text => fund.ETFName.toLowerCase().indexOf(text.toLowerCase()) > -1))
            } else if (type === 2) {
                temp = filteredFunds.filter(fund => textArray.every(text => fund.FundName.toLowerCase().indexOf(text.toLowerCase()) > -1))
            } else {
                temp = filteredFunds.filter(fund => textArray.every(text => fund.MintboxName.toLowerCase().indexOf(text.toLowerCase()) > -1))
            }
        }
        setSearchResult(temp)
    }

    const onSort = async (durationId) => {
        console.log("sort", durationId, searchResult)
        let temp = searchResult.slice();
        if (type !== 3) {
            if (durationId === 0) {
                temp.sort((a, b) => b.OneYrReturn - a.OneYrReturn)
            } else if (durationId === 1) {
                temp.sort((a, b) => b.ThreeYrReturn - a.ThreeYrReturn)
            } else if (durationId === 2) {
                temp.sort((a, b) => b.FiveYrReturn - a.FiveYrReturn)
            }
        }
        setSortResult(temp)
        setData(temp.slice(0, 25)) // set initial 25 funds
        // window.scrollTo({ top: 0, behavior: 'auto' })
    }


    const loadMoreFunds = () => {
        setData(sortResult.slice(0, data.length + 10)) // set data with 10 additionalfunds
    };




    

    const debounce = (func, wait = 20, immediate = true) => {
        let timeOut;
        return () => {
            let context = this,
                args = arguments;
            const later = () => {
                timeOut = null;
                if (!immediate) func.apply(context, args);
            };
            const callNow = immediate && !timeOut;
            clearTimeout(timeOut);
            timeOut = setTimeout(later, wait);
            if (callNow) func.apply(context, args);
        };
    };


    

    const ETFView = () => {
        return (
            <>
                <InfiniteScroll
                    dataLength={sortResult.length} //This is important field to render the next data
                    next={() => loadMoreFunds()}
                    hasMore={data.length < sortResult.length}
                    loader={<center>
                        <Spinner
                            as="span"
                            animation="grow"
                            size="sm"
                            role="status"
                            aria-hidden="true" />
                    </center>}
                >
                    {
                        data.map((fund, i) =>
                            <div key={i} style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }} onClick={() => props.history.push("/ETF-detail/" + fund.Symbol)}>
                                <div style={{ display: "flex", gap: "4%" }}>
                                    <AMCIcon amcName={fund.AMCName} height='40px' width='40px' />
                                    <div style={{ flex: "1" }}>
                                        <div className="header2dark lineClamp">{fund.ETFName}</div>
                                        <div className='smallTextLight' style={{ marginTop: '1%' }}>{fund.Asset}</div>
                                    </div>
                                    <div className='header1dark'>
                                        {
                                            duration === 2 ?
                                                <RedGreenText text={parseFloat(fund.FiveYrReturn || 0).toFixed(2) + '%'} value={fund.FiveYrReturn} />
                                                :
                                                duration === 1 ? <RedGreenText text={parseFloat(fund.ThreeYrReturn || 0).toFixed(2) + '%'} value={fund.ThreeYrReturn} />
                                                    :
                                                    <RedGreenText text={parseFloat(fund.OneYrReturn || 0).toFixed(2) + '%'} value={fund.OneYrReturn} />
                                        }
                                    </div>
                                </div>
                            </div>
                        )
                    }
                </InfiniteScroll>
            </>
        )
    }

    const MFView = () => {
        return (
            <InfiniteScroll
                dataLength={sortResult.length} //This is important field to render the next data
                next={() => loadMoreFunds()}
                hasMore={data.length < sortResult.length}
                loader={<center>
                    <Spinner
                        as="span"
                        animation="grow"
                        size="sm"
                        role="status"
                        aria-hidden="true" />
                </center>}
            >
                {
                    data.map((fund, i) =>
                        <div key={i} style={{ padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }} onClick={() => props.history.push("/fund-detail/" + fund.FundCode)}>
                            <div style={{ display: "flex", gap: "4%" }}>
                                <AMCIcon amcName={fund.AMCName} height='40px' width='40px' />
                                <div style={{ flex: "1" }}>
                                    <div className="header2dark lineClamp">{fund.FundName}</div>
                                    <div className='smallTextLight' style={{ marginTop: '1%' }}>{fund.SchemeType}</div>
                                </div>
                                <div className='header1dark'>
                                    {
                                        duration === 2 ?
                                            <RedGreenText text={parseFloat(fund.FiveYrReturn || 0).toFixed(2) + '%'} value={fund.FiveYrReturn} />
                                            :
                                            duration === 1 ? <RedGreenText text={parseFloat(fund.ThreeYrReturn || 0).toFixed(2) + '%'} value={fund.ThreeYrReturn} />
                                                :
                                                <RedGreenText text={parseFloat(fund.OneYrReturn || 0).toFixed(2) + '%'} value={fund.OneYrReturn} />
                                    }
                                </div>
                            </div>
                        </div>
                    )
                }
            </InfiniteScroll>
        )
    }

    const MintboxView = () => {
        return (
            <InfiniteScroll
                dataLength={sortResult.length} //This is important field to render the next data
                next={() => loadMoreFunds()}
                hasMore={data.length < sortResult.length}
                initialScrollY={0}
                loader={<center>
                    <Spinner
                        as="span"
                        animation="grow"
                        size="sm"
                        role="status"
                        aria-hidden="true" />
                </center>}
            >
                {
                    data.map(mintbox =>
                        <div key={mintbox.MintboxId} style={{ padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }} onClick={() => { props.history.push("/mintbox-detail/" + mintbox.MintboxId) }}>
                            {
                                mintbox.RiskLevel === clientData?.RiskInfo?.[0]?.RiskScore ?
                                    <div style={{ marginBottom: "4%" }}>
                                        <span className="smallTextLight" style={{ backgroundColor: cssVar.getPropertyValue('--secondaryGreen'), padding: "4px 8px", borderRadius: "4px", whiteSpace: "nowrap", color: cssVar.getPropertyValue('--white') }}>
                                            <img src={baseImgURL+'like-white.webp'} width='14px' height='14px' style={{ marginRight: "4px" }} /> Recommended for you
                                        </span>
                                    </div>

                                    :
                                    <></>
                            }
                            <div style={{ display: "flex", gap: "4%" }}>
                                <MintboxIcon risk={mintbox.RiskLevel} height="40px" width="40px" />
                                <div style={{ flex: "1" }}>
                                    <div className='header1dark'>{mintbox.MintboxName}</div>
                                </div>
                            </div>
                            <div className='bodyTextLight' style={{ margin: '4% 0', whiteSpace: 'pre-line' }}>
                                <ReadMoreText text={mintbox.About?.replaceAll(`\{\"\\n\"\}`, '\n\n')} />
                            </div>
                            <div style={{ display: "flex" }}>
                                <div style={{ flex: 1 }}>
                                    <div className='smallTextLight'>5Y CAGR</div>
                                    <div className='header2dark'>
                                        <RedGreenText text={parseFloat(mintbox.XIRR || 0).toFixed(2) + '%'} value={mintbox.XIRR || 0} />
                                    </div>
                                </div>
                                <div style={{ flex: 1 }}>
                                    <div className='smallTextLight'>Min investment</div>
                                    <div className='header2dark'>{CurrencyFormat(mintbox.MinSIPAmount)}</div>
                                </div>
                                <div style={{ flex: 1 }}>
                                </div>
                            </div>
                        </div>
                    )
                }
            </InfiniteScroll>
        )
    }

    return (
        <div className='mainContainer'>
            <div className='sticky-top' style={{ backgroundColor: cssVar.getPropertyValue("--white") }}>
                <MarketingHeader props={props}  />
                <div className='searchBar' style={{marginTop:"64px"}}>
                    <FiSearch size={20} className='secondaryGreen' style={{ margin: "0 4%" }} />
                    <input className='searchInput' placeholder={type === 1 ? 'Search by ETF name' : type === 2 ? 'Search by mutual fund name' : 'Search by mintbox name'} value={searchText}
                        onChange={(e) => { setSearchText(e.target.value); onSearch(e.target.value) }}
                    />
                </div>
                <div style={{ background: cssVar.getPropertyValue('--lightest'), border: "1px solid " + cssVar.getPropertyValue('--light'), padding: "2% 4%" }} className='hiddden-scrollbar'>
                    {
                        chipset.filter(c => c.show !== false).map(chip => {
                            return <Chip key={chip.key} label={chip.name}
                                className={selectedFiltersArray.includes(chip.key) ? "normalChip selected" : "normalChip"}
                                onClick={() => { setSelectedFiltersArray([chip.key]); setDuration(0) }}
                            />
                        })
                    }
                </div>
                {
                    type === 3 ?
                        <></>
                        :
                        <>
                            <div style={{ display: "flex", alignItems: 'center', padding: "4% 4% 1% 4%" }}>
                                <div className='header1dark' style={{ flex: 1 }}>
                                    {selectedFiltersArray.length === 1 ? chipset.find(c => c.key === selectedFiltersArray[0])?.name : 'Filtered'}
                                    {type === 1 ? ' ETFs' : type === 2 ? ' mutual funds' : type === 3 ? ' mintboxes' : ''}
                                </div>
                                <button onClick={() => { setDuration((duration + 1) % 3); onSort((duration + 1) % 3) }} className='greenChipHeader' style={{ minWidth: '120px' }}>
                                    <BsChevronLeft style={{ paddingRight: "5px" }} size={14} />
                                    {
                                        duration === 2 ? "5 year return" : duration === 1 ? "3 year return" : "1 year return"
                                    }
                                    <BsChevronRight style={{ paddingLeft: "5px" }} size={14} />
                                </button>
                            </div>
                            <div style={{ height: '3px', width: '8%', marginLeft: '4%', backgroundColor: cssVar.getPropertyValue('--primaryGreen'), borderRadius: '50%' }}></div>
                        </>
                }
            </div>
            <div className='dataContainer px-0' >
                {
                    loading ?
                        <ActivityLoaderMintbox />
                        :
                        <div>
                            {
                                selectedFiltersArray.includes("Recommended") && !clientData?.RiskInfo[0]?.RiskScore ?
                                    <div style={{ padding: "4%", margin: "4% 10%", textAlign: "center" }}>
                                        <img src={baseImgURL+"not-found.webp"} width="50%" style={{ margin: "4% auto" }} />
                                        <button className='solidButton'
                                            onClick={() => { props.history.push('/risk-profile') }}
                                        >
                                            Complete risk profile
                                        </button>
                                    </div>
                                    :
                                    sortResult.length === 0 ?
                                        <div style={{ padding: "4%", margin: "4% 10%", textAlign: "center" }}>
                                            <img src={baseImgURL+"not-found.webp"} width="50%" style={{ margin: "4% auto" }} />
                                            <div style={{ fontSize: "18px", fontWeight: "500", margin: "4% 0%" }}>
                                                No results!
                                            </div>
                                        </div>
                                        :
                                        type === 1 ? <ETFView /> : type === 2 ? <MFView /> : type === 3 ? <MintboxView /> : ''
                            }
                        </div>
                }
            </div>
        </div>
    )
}


























